.section {
    background-color: #1d2022;
    position: relative;
    z-index: 300;
    padding-bottom: 200vw;
    margin-left: -2vw;
    margin-right: -2vw;
    margin-bottom: -2vw;
}

.about-me {
    font-family: "Open Sans", sans-serif;
    font-size: 40px;
    letter-spacing: 2px;
    color: white;
    -webkit-text-stroke: .1vw black;
    text-align: center;
    padding-top: 50px;
}

.text {
    font-family: "Open Sans", sans-serif;
    font-size: 2vw;
    letter-spacing: .1vw;
    color: white;
    text-align: left;
    padding-left: 5vw;
    padding-right: 5vw;
}

@media (max-width: 800px) {
    .about-me {
        font-size: 4vw;
        -webkit-text-stroke: .1vw black;
        padding-top: 4vw;
    }
}