.menu-bar {
    padding: 1vw;
    justify-content: space-between;
    background-color: #474b4f;
    top: 0px;
    left: 0px;
    width: 100%;
    overflow: hidden;
    position: fixed;
    z-index: 400;
    display: flex;
}

.my-name {
    font-family: "Open Sans", sans-serif;
    font-size: 1.6vw;
    letter-spacing: .1vw;
    text-transform: uppercase;
    margin-right: 5vw;
    color: white;
    -webkit-text-stroke: .05vw black;
}

.version {
    font-size: .7vw;
    position: absolute;
    color: gray;
    top: 60%;
    z-index: 401;
    right: 6.5vw;
}