.image {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-top: 9vw;
    margin-bottom: 4.5vw;
    max-width: 50%;
    height: 30vw;
    border-radius: 1vw;
    animation-name: fade;
    animation-duration: 4s;
    object-fit: contain;
    position: relative;
    z-index: 200;
}

@keyframes fade {
    from {opacity: 0;}
    to {opacity: 1;}
}

.background-img {
    width: 100%;
    left: 0px;
    right: 0px;
    top: 0;
    height: 100%;
    position: fixed;
    z-index: 100;
}

@media (max-width: 1200px){
    .background-img {
        top: -400px;
    }
}